<template>
    <div class="blog">
        <div class="g-bread">
            <div class="bread-crumb">
                <el-breadcrumb separator=">">
                    <el-breadcrumb-item :to="{ path:'/' }">首页</el-breadcrumb-item>
                    <el-breadcrumb-item>博客</el-breadcrumb-item>
                </el-breadcrumb>
                <h2 class="b-dis">博客首页</h2>
            </div>
        </div>
        <div class="main-con clearfix" v-if="article">
            <div class="side clearfix">
                <div class="search">
                    <h3 class="m-subtitle">博客搜索</h3>
                    <el-input
                        placeholder="搜索"
                        icon="search"
                        v-model="search"
                        >
                    </el-input>
                </div>
                <div class="recent-post">
                    <h3 class="m-subtitle">最近博客</h3>
                    <div v-for="item in articles.data" :key="item.new_id">
                        <dl class="post-item">
                            <dt class="blog-title">
                                <router-link :to="{name:'Article', params: { id: item.new_id }}">{{item.new_title}}</router-link>
                            </dt>
                            <dd class="b-r-img"><img :src='item.new_thumb' alt="" width="70px" height="41px"></dd>
                            <dd class="post-time">
                                <i class="el-icon-date"></i>
                                <span>{{item.created_at.date}}</span>
                            </dd>
                        </dl>
                    </div>
                </div>
                <div class="archives">
                    <h3 class="m-subtitle">档案</h3>
                    <el-tree :data="data" ></el-tree>
                </div>
                <div class="popular-tags">
                    <h3 class="m-subtitle">热门标签</h3>
                    <el-tag type="success">鲜花礼品</el-tag>
                </div>
            </div>
            <div class="blog-main clearfix">
                <div class="latest-blog">
                    <div class="blog-banner">
                        <img :src='article[0].new_thumb' alt="">
                        <div class="blog-detail">
                            <span>作者：</span> {{article[0].new_editor? article[0].new_editor : '未知'}} 
                            <i class="el-icon-date"></i> {{article[0].created_at}}
                        </div>
                    </div>
                    <article class="blog-article">
                        <header class="b-header"><h4>{{article[0].new_title}}</h4></header>
                        <section class="b-sec">
                            {{article[0].new_description}}
                        </section>
                        <footer class="b-foot">
                            <router-link :to="{name:'Article',params:{ id:article[0].new_id}}">Read More ></router-link>
                        </footer>
                    </article>
                </div>
                <div class="blog-card clearfix">
                    <el-row v-if="news">
                        <el-col :span="8" v-for="item in news.data" :key="item.new_id">
                        <router-link :to="{name:'Article' ,params: { id: item.new_id }}">
                            <el-card class="blog-card-item" :body-style="{padding:'0'}">
                                <img src="../assets/img/articleimg.jpg" class="image">
                                <div>
                                    <h5>{{item.new_title}}</h5>
                                    <div class="b-c-i-d">
                                        {{item.new_description}}
                                    </div>
                                </div>
                                <span class="read-more">
                                    Read More >  
                                </span>
                            </el-card>
                            </router-link>
                        </el-col>
                    </el-row>
                </div>
                <div class="page" v-if="news">
                    <el-pagination
                        @current-change="getResults"
                        layout="prev, pager, next"
                        :page-count='news.last_page'>
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default{
        data(){
            return {
                search: '',
                news: null,
                article:null,
                data:null
            }
        },
        computed: {
            articles(){
                return this.$store.state.articles;
            }
        },
        created(){
        	this.fetchData();
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData () {
                this.$ajax.get('http://www.lthuali.com/api/articles').then((response)=>{
                    this.news = response.data;
                });
            },
            getResults(page){ 
                if(typeof page === undefined){
					page = 1;
				}
				this.$ajax.get('http://www.lthuali.com/api/articles?page=' + page).then((response)=>{
					this.news = response.data;
				})
            }
        },
        mounted(){
            this.$ajax.get('http://www.lthuali.com/api/recentArticle').then((response)=>{
                this.article = response.data;
            });
        }
    }
</script>

<style scoped>

    /*博客上部分和左侧通用样式*/

    .main-con {
        position: relative;
        width: 1170px;
        height: 1780px;
        margin: 0 auto;
    }
    .main-con .side {
        position: relative;
        float: left;
        margin-top: 60px;
        width: 270px;
        height: 1300px;
        overflow: hidden;
    }
    .main-con .side .m-subtitle {
        width: 270px;
        font-size: 18px;
        font-weight: normal;
        margin-bottom: 20px;
    }
    .main-con .side .b-categories{
        position: relative;
        margin-top: 20px;
    }
    .main-con .side .recent-post{
        position: relative;
        float: left;
        margin-top: 20px;
        width: 270px;
    }
    .main-con .side .recent-post .post-item{
        position:relative;
        margin-bottom: 26px;
        width: 270px;
        height: 54px;
    }
    .main-con .side .recent-post .post-item .blog-title{
        position: absolute;
        width: 170px;
        left: 100px;
        top: 5px;
        font-size: 14px;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
    }
    .main-con .side .recent-post .post-item .b-r-img{
        position: absolute;
        left: 0;
        width: 70px;
        height: 54px;
    }
    .main-con .side .recent-post .post-item .post-time{
        position: absolute;
        left: 100px;
        bottom: 5px;
        color: #999999;
    }
    .main-con .side .popular-tags{
        margin-top: 20px;
        width: 270px;
    }

    /*博客主页专有样式*/
    .blog-main{
        float: right;
        width:870px;
        height: 1780px;
        overflow: hidden;
    }
    .blog-main .latest-blog{
        position: relative;
        width: 870px;
        height: 710px;
        border-bottom: 1px solid #eaeaea;
    }
    .blog-main .latest-blog .blog-banner{
        position: relative;
        margin-top: 60px;
        width: 870px;
        height: 518px;
    }
    .blog-main .latest-blog .blog-banner .blog-detail{
        position: absolute;
        bottom: 0;
        text-indent: 2em;
        width: 870px;
        height: 38px;
        line-height: 38px;
        z-index: 8;
        color: #fff;
    }
    .blog-main .latest-blog .blog-article{
        margin-top: 24px;
        width: 870px;
    }
    .blog-main .latest-blog .blog-article .b-header>h4{
        width: 870px;
        font-size: 18px;
    }
    .blog-main .latest-blog .blog-article .b-sec{
        margin-top: 22px;
        font-size: 14px;
        line-height: 16px;
    }
    .blog-main .latest-blog .blog-article .b-foot{
        margin-top: 26px;
        font-size: 14px;
    }
    .blog-main .blog-card{
        position: relative;
        margin-top: 46px;
        width: 870px;
        height: 940px;
    }
    .blog-main .blog-card .blog-card-item{
        width: 270px;
        height: 410px;
        margin: 8px;
    }
    .blog-main .blog-card .blog-card-item h5{
        margin-top: 20px;
        width: 270px;
        font-size: 15px;
        line-height:20px;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
    }
    .blog-main .blog-card .blog-card-item .b-c-i-d{
        margin-top: 20px;
        font-size: 14px;
        line-height: 20px;
        height: 140px;
        color: #999;
        text-overflow: ellipsis;
        overflow: hidden;
        display: -webkit-box;
        line-clamp: 3;
        -webkit-box-orient: vertical;

    }
    .blog-main .blog-card .blog-card-item .read-more{
        display: block;
        margin-top: 20px;
    }
    .blog-main .page{
        position:absolute;
        bottom: 40px;
        left: 50%;
        -webkit-transform: translateX(-20%);
        -moz-transform: translateX(-20%);
        -ms-transform: translateX(-20%);
        -o-transform: translateX(-20%);
        transform: translateX(-20%);
    }

</style>



