<template>
    <div class="article" v-if="article">
        <div class="g-bread">
            <div class="bread-crumb">
                <el-breadcrumb separator=">">
                    <el-breadcrumb-item :to="{ path:'/' }">首页</el-breadcrumb-item>
                    <el-breadcrumb-item>{{ article.new_title ? article.new_title: '未知文章' }}</el-breadcrumb-item>
                </el-breadcrumb>
                <h2 class="b-dis">{{ article.new_title? article.new_title: '未知文章' }}</h2>
            </div>
        </div>
        <div class="main-con clearfix">
            <div class="blog-main clearfix">
            <div class="blog-article">
                <div class="blog-banner">
                    <img :src= "article.new_thumb" alt="">
                    <div class="blog-detail"><span>作者：{{ article.new_editor }}</span>
                        <i class="el-icon-date"></i> {{article.created_at}}
                    </div>
                </div>
                <section class="b-article" v-html = 'article.new_content' >

                </section>
                <footer class="tag">
                    <p><span>标签：</span>Flower leis, Granduation, Lei, Leis, Meagan bowman</p>
                </footer>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
    export default{
    	name:'Article',
        data(){
            return {
                article:null,
                search:'',
            }
        },
        created(){
        	this.$ajax.get('http://www.lthuali.com/api/article/'+ this.$route.params.id).then((response)=>{
        		this.article = response.data;
            })
        }
    }
</script>

<style>
    .main-con{
        margin: 0 auto;
        width: 1170px;
        font-size: 16px;
        line-height: 18px;
    }
    .main-con strong{
        font-size: 18px;
        line-height: 20px;
    }
</style>



