<template>
    <div class="header" id="header">
        <div class="g-tb">
            <div class="m-tb">
                <ul class="u-tb u-tbl">
                    <li class="u-li">欢迎访问兰亭花礼网</li>
                </ul>
                <ul class="u-tb u-tbr">
                    <li class="u-li u-sp u-name"><a href="#"><span>欢迎：{{userName}} </span></a></li>
                    <li class="u-li u-sp"  v-if="!this.$cookie.get('user_name')">
                        <el-button type="text" @click="checkCookie">登录</el-button>
                        <el-dialog title="登录" v-model="FormVisible" size="tiny">
                            <el-form :model="form">
                                <el-form-item label="账 号" :label-width="formLabelWidth">
                                    <el-input v-model="form.name" placeholder="请输入邮箱" auto-complete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="密 码" :label-width="formLabelWidth">
                                    <el-input type="password" placeholder="请输入密码" v-model="form.password"></el-input>
                                </el-form-item>
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="FormVisible = false">取 消</el-button>
                                <el-button type="primary" @click="login" :disabled= btndsiabled>确 定</el-button>
                            </div>
                        </el-dialog>
                    </li>
                    <li class="u-li"  v-if="!this.$cookie.get('user_name')">
                        <el-button type="text" @click="dialogFormVisible = true">注册</el-button>
                        <el-dialog title="注册" v-model="dialogFormVisible" size="tiny">
                            <el-form :model="form">
                                <el-form-item label="账 号" :label-width="formLabelWidth">
                                    <el-input v-model="form.name" placeholder="请输入邮箱" auto-complete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="密 码" :label-width="formLabelWidth">
                                    <el-input type="password" placeholder="密码不为空且大于6位小于16位的字母或数字
                                    " v-model="form.password"></el-input>
                                </el-form-item>
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="dialogFormVisible = false">取 消</el-button>
                                <el-button type="primary" @click="register" :disabled= btndsiabled >确 定</el-button>
                            </div>
                        </el-dialog>
                    </li>
                    <li class="u-li u-sp u-name" v-if="this.$cookie.get('user_name')"><a @click="unLogin"><span> 退 出</span></a></li>
                </ul>
            </div>
        </div>
        <div class="g-hd">
            <div class="m-lg">
                <router-link :to="{path:'/'}">
                    <span class="u-logo"></span>
                    <h1 class="u-title">兰亭花礼苑</h1>
                </router-link>
            </div>
            <div class="m-nav">
                <ul class="u-nav">
                    <li><router-link :to="{path:'/'}" ><span class="nav-item">首页</span></router-link</li>
                    <li>
                        <router-link :to="{path:'/products'}"><span class="nav-item">商品列表</span></router-link>
                    </li>
                    <li>
                        <router-link :to="{path:'/news'}"><span class="nav-item">相关资讯</span></router-link>
                    </li>
                    <li>
                        <router-link :to="{path:'/active'}"><span class="nav-item">热门活动</span></router-link>
                    </li>
                    <li>
                        <router-link :to="{path:'/aboutus'}"><span class="nav-item">关于我们</span></router-link>
                    </li>
                </ul>
            </div>
            <div class="sear-cart">
                <span class="search"><img src="../../assets/img/searc.png" alt="搜索"></span>
                <span class="cart"><img src="../../assets/img/cart.png" alt="购物车">
                    <i class="cart-num" @click="showCart">{{cartNumber}}</i>
                </span>
                <div class="cart-list" v-if="showcart">
                   <el-card class="cart-card clearfix" v-for="(o,index) in cart" :key="o.cart_id">
                        <img class="cart-img" :src=o.commodity_pic alt="o.commodity_name">
                        <span class="c-name">{{o.commodity_name}}</span>
                        <span class="c-num">数量：{{o.number}}</span>
                        <span class="c-total">价格：{{Number(o.number) * Number(o.price)}}</span>
                        <div class="cart-btn">
                            <el-button type="warning" @click='deleteCart(index)'>删除</el-button>
                        </div>
                    </el-card>
                </div>
            </div>
        </div>
    </div>
</template>

<script> 
    import axios from 'axios'
	export default{
		name:'MainNav',
		data(){
			return {
				dialogFormVisible: false,
                FormVisible : false,
                btndsiabled : false,
                form: {
                    name: '',
                    password:'',
                },
                formLabelWidth: '100px',
                userName:this.$cookie.get('user_name'),
                cart:null,
                showcart:false,
                total:null
			}
		},
        created(){
            return this.getCartInfo();
        },
        methods: {
            register() {
                this.btndsiabled = true;
                if(this.form.name && this.form.password){
                    const pattern = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/g
                    if(pattern.test(this.form.name)){
                        axios.post('http://www.lthuali.com/api/register',{name:this.form.name,password:this.form.password}).then((response)=>{
                            if(response.data.status){
                                this.$notify({
                                    title: '成功',
                                    message: '注册成功',
                                    type: 'success',
                                });
                                this.btndsiabled = false;
                                this.form.name = null;
                                this.form.password = null;
                                setTimeout(()=>{
                                    this.dialogFormVisible = false;
                                },1000);
                            }else{
                                this.$notify.error({
                                    title: '错误',
                                    message: response.data.msg
                                });
                                this.btndsiabled = false;
                            }
                        });
                    }else{
                        this.$notify({
                            title: '警告',
                            message: '请输入正确的邮箱',
                            type: 'warning'
                        });
                        this.btndsiabled = false;
                    }
                }else{
                    this.$notify({
                        title: '警告',
                        message: '请输入用户邮箱或密码',
                        type: 'warning'
                    });
                    this.btndsiabled = false;
                }
            },
            login(){
                if(this.$cookie.get('user_name')){
                    this.FormVisible = false;
                    return this.$notify({
                        title: '警告',
                        message: '您已登录，请勿重复登录',
                        type: 'warning'
                    });
                }
                if( !(this.form.name && this.form.password)){
                    return this.$notify({
                        title: '警告',
                        message: '请输入用户邮箱及密码',
                        type: 'warning'
                    });
                }
                this.btndsiabled = true;
                axios.post('http://www.lthuali.com/api/login',{name:this.form.name,password:this.form.password}).then((response)=>{
                    if(response.data.status){
                        this.$notify({
                            title: '成功',
                            message: response.data.msg,
                            type: 'success',
                        });
                        this.$cookie.set('user_name', this.form.name , 1);
                        this.btndsiabled = false;
                        this.form.name = null;
                        this.form.password = null;
                        setTimeout(()=>{
                            this.FormVisible = false;
                            location.reload();
                        },1000);
                    }else{
                        this.$notify.error({
                            title: '登录失败',
                            message: response.data.msg,
                        });
                        this.btndsiabled = false;
                    }
                });
            },
            checkCookie(){
                if(this.$cookie.get('user_name')){
                    this.FormVisible = false;
                    return this.$notify({
                        title: '警告',
                        message: '您已登录，请勿重复登录',
                        type: 'warning'
                    });
                }else{
                    this.FormVisible = true;
                }
            },
            getCartInfo(){
                let username = this.$cookie.get('user_name');
                if(username){
                    this.$ajax.get('http://www.lthuali.com/api/cart/' + username).then((response)=>{
                        this.cart = response.data
                    })
                }
            },
            showCart(){
                if(!this.$cookie.get('user_name')){
                    return this.$notify({
                        title: '警告',
                        message: '请登陆后显示购物车！',
                        type: 'warning'
                    });
                }else if(this.cart.length === 0){
                    return this.$notify({
                        title: '警告',
                        message: '请添加商品到购物车',
                        type: 'warning'
                    });
                }
                return this.showcart = !this.showcart;
            },
            deleteCart(index){
                let cartid = this.cart[index].cart_id;
                this.$ajax.delete('http://www.lthuali.com/api/delete?cart_id=' +  cartid).then((response)=>{
                    if(response.data.status){
                        this.$message({
                            message: response.data.msg,
                            type: 'success'
                        });
                        this.cart.splice(index,1);
                    }else{
                        this.$message({
                            message: response.data.msg,
                            type: 'warning'
                        });
                    }
                })
            },
            unLogin(){
                this.$cookie.delete('user_name');
                location.reload();
            }   
        },
        computed:{
            cartNumber(){
                return this.cart ? this.cart.length : 0; 
            }
        }
	}
</script>

<style scoped>


</style>



