<template>
    <div class="detail">
        <div class="item"  v-if="product">
            <div class="g-bread">
                <div class="bread-crumb">
                    <el-breadcrumb separator=">">
                        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item :to="{ path:'/products' }">商品列表</el-breadcrumb-item>
                        <el-breadcrumb-item>{{product.commodity_name}}</el-breadcrumb-item>
                    </el-breadcrumb>
                    <h2 class="b-dis">{{product.commodity_name}} {{product.commodity_short}}</h2>
                </div>
            </div>
            <div class="buy">
                <div class="details clearfix">
                    <div class="d-img">
                        <div class="show-img">
                            <div class="s-img">
                                <img :src='product.commodity_pic' :alt='product.commodity_name' width="100%">
                            </div>
                            <ul class="show-item">
                                <li class="img-item active"><img width="100%" :src='product.commodity_pic' :alt='product.commodity_name'></li>
                                <li class="img-item"><img width="100%" :src='product.commodity_pic' :alt='product.commodity_name'></li>
                                <li class="img-item"><img width="100%" :src='product.commodity_pic' :alt='product.commodity_name'></li>
                                <li class="img-item"><img width="100%" :src='product.commodity_pic' :alt='product.commodity_name'></li>
                            </ul>
                        </div>
                    </div>
                    <div class="d-items clearfix">
                        <h2 class="fg-name">{{ product.commodity_name}}</h2>
                        <ul class="shop">
                            <li class="shop-item">
                                <el-rate
                                    v-model="product.star"
                                    disabled
                                    text-color="#ff9900">
                                </el-rate>
                            </li>
                            <li class="shop-item"><span>￥{{ product.price }}</span> <s>￥ {{ product.old_price }}</s></li>
                            <li class="shop-item"><span>编号：</span>{{ product.commodity_id }}</li>
                            <li class="shop-item"><span>类别：</span>{{ product.category }}</li>
                            <li class="shop-item"><span>包装：</span>{{ product.package }}</li>
                            <li class="shop-item"><span>材料：</span>{{ product.material }}</li>
                            <li class="shop-item"><span>花语：</span>{{ product.flw_lan }}</li>
                            <li class="shop-item"><span>配送：</span>{{ product.distribution }}</li>
                            <li class="shop-item"><span>附送：</span>{{ product.include}}</li>
                            <li class="shop-item ls-num">
                                <span class="s-num">数量：</span>
                                <el-input-number class="shop-num" v-model="num" :min="1"
                                                 :max="10"></el-input-number>
                            </li>
                        </ul>
                        <div class="buy-button">
                            <el-button @click="addCart">加入购物车</el-button>
                        </div>
                    </div>
                </div>
            </div>
            <div class="infor clearfix">
                <el-tabs v-model="activeName">
                    <el-tab-pane label="细节展示" name="first">
                        <img :src=product.details alt="细节展示">
                    </el-tab-pane>
                    <el-tab-pane label="设计师物语" name="second">
                       <img :src=product.design alt="细节展示">
                    </el-tab-pane>
                    <el-tab-pane label="选择理由" name="third">
                       <img :src=product.resion alt="选择理由">
                    </el-tab-pane>
                </el-tabs>
            </div>
            <div class="relate">
                <h3>相关产品</h3>
                <el-row>
                    <el-col :span="6" v-for="item in products.flowers" :key="item.commodity_id" v-if="item.commodity_id !== product.commodity_id">
                        <router-link :to="{ name:'ProductDetail', params : { id: item.commodity_id }}">
                            <el-card class="card" :body-style="{padding:'0'}">
                                <img :src="item.commodity_pic" class="image">
                                <h3>{{item.commodity_name}}</h3>
                                <span>￥{{item.price}}</span> ￥<s>{{item.old_price}}</s>
                                <div class="star">
                                    <el-rate
                                        v-model = item.star
                                        disabled
                                        text-color="#ff9900">
                                    </el-rate>
                                </div>
                            </el-card>
                        </router-link>
                    </el-col>
                </el-row>
            </div>
        </div>
    </div>
</template>

<script>
    export default{
        created () {
            this.fetchData();
        },
        computed:{
            products(){
                let products = this.$store.state.products;
                if(products.length>4){
                    return products.splice(0,4);
                }else{
                    return products;
                }
            }
        },
        data () {
            return {
                activeName:'first',
                error: null,
                num:1,
                product:null,
                cartItem:{
                    user_name:null,
                    commodity_id:null,
                    commodity_pic:null,
                    commodity_name:null,
                    price:null,
                    number:null,
                },
            }
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData () {
                this.$ajax.get('http://www.lthuali.com/api/product/'+ this.$route.params.id).then((response)=>{
                    this.product = response.data;
                });
            },
            addCart(){
                if(!this.$cookie.get('user_name')){
                    return this.$message({
                            message: '请登录后添加购物车',
                            type: 'warning'
                        });
                }
                this.cartItem.user_name = this.$cookie.get('user_name');
                this.cartItem.commodity_id = this.product.commodity_id;
                this.cartItem.commodity_pic = this.product.commodity_pic;
                this.cartItem.commodity_name = this.product.commodity_name;
                this.cartItem.price = this.product.price;
                this.cartItem.number = this.num;
                this.$ajax.post('http://www.lthuali.com/api/addcart',this.cartItem).then((response)=>{
                    if(response.data.status){
                        this.$message({
                            message: response.data.msg,
                            type: 'success'
                        });
                        setTimeout(function(){
                            location.reload();
                        },300);
                    }else{
                        this.$message({
                            message: response.data.msg,
                            type: 'warning'
                        });
                    }
                })
                this.cartItem = {user_name:null,commodity_id:null,commodity_pic:null,commodity_name:null,price:null,number:null};
            }
        },
    }
</script>

<style>

    .details {
        width: 1170px;
        height: 600px;
    }

    .buy {
        width: 1170px;
        height: 600px;
        margin: 65px auto;
        background-color:#fefefe;
    }
    .buy .d-img{
        width:470px;
    }
    .show-img {
        position: relative;
        float: left;
        width: 470px;
        height: 600px;
        overflow: hidden;
    }

    .show-img .s-img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform:translate(-50%,-70%);
        width: 370px;
        height: 500px;
    }

    .show-img .show-item {
        position: absolute;
        bottom: 0;
        width: 440px;
        height: 144px;
    }

    .show-img .show-item .img-item {
        display: inline-block;
        width: 100px;
        height: 144px;
        margin-right:5px;
    }

    .d-items {
        position:relative;
        float: right;
        width: 670px;
        height: 800px;
        margin-top:20px;
    }

    .d-items .fg-name {
        font-size: 30px;
        height: 50px;
    }

    .shop {
        height: 400px;
        width: 670px;
    }

    .shop :nth-child(even) {
        border-bottom: 1px solid #f4f4f4;
    }

    .shop-item {
        width: 670px;
        height: 35px;
        overflow: hidden;
        font-size: 16px;
        line-height: 30px;
    }

    .shop-item > span {
        font-size: 18px;
        line-height: 30px;
    }

    .shop-item > p {
        display: inline-block;
        font-size: 16px;
        line-height: 30px;
    }

    .ls-num {
        height: 70px;
    }

    .s-num {
        display: inline-block;
        margin-top: 30px;
    }

    .shop-item .shop-num {
        top: 10px;
    }

    .buy-button {
        margin-top: 20px;
        height: 70px;
        width: 670px;
        border-bottom: 1px solid #f4f4f4;
    }

    .infor {
        position: relative;
        width: 1170px;
        margin: 60px auto 0;
    }
    .infor .el-tabs .el-tabs__content{
        width:1170px;
        top:-100px;
    }
    .infor .el-tabs .el-tabs__content .el-tab-pane{
        width:100%;
    }
    .infor .el-tabs .el-tabs__content .el-tab-pane>img{
        display:block;
        margin:0 auto;
    }
    .relate {
        position:relative;
        width: 1170px;
        height: 560px;
        margin:0 auto;
        overflow: hidden;
    }

    .relate > h3 {
        width: 1170px;
        height: 60px;
        font-size: 28px;
        font-weight: normal;
        text-align: center;
    }

    .card {
        display: inline-block;
        width: 270px;
        height: 460px;
        text-align: center;
        margin: 8px;
    }
    img + h3 {
        width: 270px;
        margin: 15px auto;
        font-size: 14px;
        font-weight: normal;
    }

    .star {
        margin: 10px;
    }
    .description{
        font-size:18px;
        line-height: 25px;
    }
</style>



