<template>
    <div class="active_main">
        <div class="active" v-for="item in active">
            <router-link :to="{name:'ActiveItem',params:{id:item.active_id}}"><img :src='item.active_big_pic' :alt='item.active_name'></router-link>
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return {
                active:null
            }
        },
        created(){
            this.$ajax.get('http://www.lthuali.com/api/allactive').then((response)=>{
                this.active = response.data;
            })
        }
    }
</script>

<style lang="">
    .active_main{
        wdith:1920px;
        margin:0 auto;
    }
</style>


