<template>
    <div class="active_main" v-if="active">
        <div class="header">
            <img :src='active.active_big_pic' :alt='active.active_name'>
        </div>
        <div class="main-content" v-if="active.active_pic">
            <div class="active-left">
                <router-link :to="{name:'ProductDetail',params:{id : active.active_pic[1].productId}}">
                    <img class="top-img" :src="active.active_pic[1].pic_url" alt="">
                </router-link>
                <router-link :to="{name:'ProductDetail',params:{id : active.active_pic[2].productId}}">
                    <img :src="active.active_pic[2].pic_url" alt="">
                </router-link>
            </div>
            <div class="active-main">
                 <router-link :to="{name:'ProductDetail',params:{id : active.active_pic[0].productId}}">
                    <img :src="active.active_pic[0].pic_url" alt="">
                </router-link>
            </div>
            <div class="active-right">
                <router-link :to="{name:'ProductDetail',params:{id : active.active_pic[3].productId}}">
                    <img class="top-img" :src="active.active_pic[3].pic_url" alt="">
                </router-link>
                <router-link :to="{name:'ProductDetail',params:{id : active.active_pic[4].productId}}">
                    <img :src="active.active_pic[4].pic_url" alt="">
                </router-link>
            </div>
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return {
                active:null
            }
        },
        created () {
            this.$ajax.get('http://www.lthuali.com/api/active/'+ this.$route.params.id).then((response)=>{
                this.active = response.data;
            });
        }
    }
</script>

<style lang="">
    .main-content{
        position:relative;
        margin:40px auto;
        width:1200px;
        height:500px;
    }
    .active-left{
        position:absolute;
        left:0;
        width: 388px;
        height: 500px;
    }
    .active-main{
        position:absolute;
        left:406px;
        width: 388px;
        height: 500px;
    }
    .active-right{
        position:absolute;
        width: 388px;
        height: 500px;
        right:0;
    }
    .main-content .top-img{
        margin-bottom: 22px;
    }
</style>


