<template>
    <div class="main">
        <el-row>
            <el-col :span="8" v-for="item in products" :key="item.commodity_id">
                <router-link :to="{name:'ProductDetail',params: { id: item.commodity_id }}">
                    <el-card class="card" :body-style="{padding:'0'}">
                        <img :src="item.commodity_pic" class="image">
                        <h3>{{item.commodity_name}}</h3>
                        <span>￥{{item.price}}</span> ￥<s>{{item.old_price}}</s>
                        <div class="star">
                            <el-rate
                                v-model = item.star
                                disabled
                                text-color="#ff9900">
                            </el-rate>
                        </div>
                    </el-card>
                </router-link>
            </el-col>
        </el-row>
    </div>
</template>

<script type="text/javascript">
    export default{
        created () {
            this.fetchData();
        },
        data () {
            return {
                products:null,
                total:null
            }
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData () {
                this.$ajax.get('http://www.lthuali.com/api/category/'+ this.$route.params.id).then((response)=>{
                    this.products = response.data;
                });
            }
        }
    }
</script>

<style type="text/css">
    .card{
        height:470px;
    }
</style>


