<template>
    <div id="app">
        <main-nav></main-nav>
        <router-view></router-view>
        <footers></footers>
    </div>
</template>

<script>
    import './assets/css/main.css'
    import MainNav from './components/ChildComponents/MainNav'
    import Footers from './components/ChildComponents/Footers'
    export default {
        data(){
            return {
                cart: 0,
            }
        },
        created () {
            this.$store.dispatch('GET_ITEMS');
            this.$store.dispatch('GET_ARTICLES_DESC')
        },
        components:{
            MainNav,Footers
        }
    }
</script>

